import React, { useEffect, useState } from 'react';
import {
    Container,
    Paper,
    Typography,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Box,
    CircularProgress,
    Alert,
} from '@mui/material';
import api from '../config/api';

interface Course {
    id: number;
    name: string;
    ects: number;
    semester: number;
    room_number: string;
}

interface Student {
    id: number;
    name: string;
    email: string;
    semester: number;
}

interface Grade {
    student_id: number;
    assignment_id: number;
    grade: number;
    submitted_at: string;
}

interface Assignment {
    id: number;
    course_id: number;
    due_date: string;
    type: string;
    description: string;
}

interface Attendance {
    student_id: number;
    course_id: number;
    classes_missed: number;
}

const TeacherDashboard: React.FC = () => {
    const [courses, setCourses] = useState<Course[]>([]);
    const [students, setStudents] = useState<{ [courseId: number]: Student[] }>({});
    const [grades, setGrades] = useState<{ [courseId: number]: Grade[] }>({});
    const [assignments, setAssignments] = useState<{ [courseId: number]: Assignment[] }>({});
    const [attendance, setAttendance] = useState<{ [courseId: number]: Attendance[] }>({});
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);

    useEffect(() => {
        const fetchTeacherData = async () => {
            try {
                // Fetch teacher's courses
                const coursesResponse = await api.get('/api/v1/courses/teacher');
                const teacherCourses = coursesResponse.data;
                setCourses(teacherCourses);

                // Fetch data for each course
                for (const course of teacherCourses) {
                    // Fetch students enrolled in the course
                    const studentsResponse = await api.get(`/api/v1/courses/${course.id}/students`);
                    setStudents(prev => ({ ...prev, [course.id]: studentsResponse.data }));

                    // Fetch assignments for the course
                    const assignmentsResponse = await api.get(`/api/v1/courses/${course.id}/assignments`);
                    setAssignments(prev => ({ ...prev, [course.id]: assignmentsResponse.data }));

                    // Fetch grades for the course
                    const gradesResponse = await api.get(`/api/v1/courses/${course.id}/grades`);
                    setGrades(prev => ({ ...prev, [course.id]: gradesResponse.data }));

                    // Fetch attendance for the course
                    const attendanceResponse = await api.get(`/api/v1/courses/${course.id}/attendance`);
                    setAttendance(prev => ({ ...prev, [course.id]: attendanceResponse.data }));
                }

                setLoading(false);
            } catch (err: any) {
                setError(err.response?.data?.detail || 'An error occurred while fetching data');
                setLoading(false);
            }
        };

        fetchTeacherData();
    }, []);

    if (loading) {
        return (
            <Box display="flex" justifyContent="center" alignItems="center" minHeight="100vh">
                <CircularProgress />
            </Box>
        );
    }

    if (error) {
        return (
            <Container>
                <Alert severity="error" sx={{ mt: 2 }}>{error}</Alert>
            </Container>
        );
    }

    return (
        <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
            <Typography variant="h4" gutterBottom>
                Teacher Dashboard
            </Typography>

            {courses.map(course => (
                <Paper key={course.id} sx={{ p: 2, mb: 2 }}>
                    <Typography variant="h5" gutterBottom>
                        {course.name}
                    </Typography>
                    <Typography variant="subtitle1" gutterBottom>
                        Room: {course.room_number} | Semester: {course.semester} | ECTS: {course.ects}
                    </Typography>

                    <Typography variant="h6" sx={{ mt: 2 }}>
                        Students
                    </Typography>
                    <TableContainer>
                        <Table size="small">
                            <TableHead>
                                <TableRow>
                                    <TableCell>Name</TableCell>
                                    <TableCell>Email</TableCell>
                                    <TableCell>Semester</TableCell>
                                    <TableCell>Classes Missed</TableCell>
                                    <TableCell>Average Grade</TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {students[course.id]?.map(student => {
                                    const studentAttendance = attendance[course.id]?.find(
                                        a => a.student_id === student.id
                                    );
                                    const studentGrades = grades[course.id]?.filter(
                                        g => g.student_id === student.id
                                    );
                                    const averageGrade = studentGrades?.length
                                        ? studentGrades.reduce((acc, curr) => acc + curr.grade, 0) / studentGrades.length
                                        : '-';

                                    return (
                                        <TableRow key={student.id}>
                                            <TableCell>{student.name}</TableCell>
                                            <TableCell>{student.email}</TableCell>
                                            <TableCell>{student.semester}</TableCell>
                                            <TableCell>{studentAttendance?.classes_missed || 0}</TableCell>
                                            <TableCell>{averageGrade}</TableCell>
                                        </TableRow>
                                    );
                                })}
                            </TableBody>
                        </Table>
                    </TableContainer>

                    <Typography variant="h6" sx={{ mt: 3 }}>
                        Assignments
                    </Typography>
                    <TableContainer>
                        <Table size="small">
                            <TableHead>
                                <TableRow>
                                    <TableCell>Type</TableCell>
                                    <TableCell>Description</TableCell>
                                    <TableCell>Due Date</TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {assignments[course.id]?.map(assignment => (
                                    <TableRow key={assignment.id}>
                                        <TableCell>{assignment.type}</TableCell>
                                        <TableCell>{assignment.description}</TableCell>
                                        <TableCell>
                                            {new Date(assignment.due_date).toLocaleDateString()}
                                        </TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </TableContainer>
                </Paper>
            ))}
        </Container>
    );
};

export default TeacherDashboard; 